<template>
  <div>
    <div class="card my-3">
      <div class="card-body">
        <h4 class="card-title  text-center">{{curPost.title}}</h4>
        <p v-html="compiledMarkdown"></p>
      </div>
    </div>
    <div class="card">
      <form class="p-4">
        <div class="form-group">
          <label for="comment_name">Name: </label>
          <input id="comment_name" type="text" class="form-control">
        </div>
        <div class="form-group">
          <label for="comment">Comment:</label>
          <textarea id="comment">

          </textarea>
        </div>
        
      </form>
    </div>
  </div>
</template>

<script>
import marked from 'marked';
export default {
  computed:{
    curPost(){
      return this.$store.getters.posts.find(ele=>ele.id === this.$route.params.id)
    },
    compiledMarkdown: function(){
      return marked(this.curPost.content, {sanitize: true});
    }
    
  },
}
</script>




