import Home from './components/Home.vue';
import PostHolder from './components/post/PostHolder.vue'
import Posts from './components/Post/Posts.vue';
import PostDetail from './components/post/PostDetail.vue';
import Works from './components/Works.vue';
import About from './components/About.vue';
import Contact from './components/Contact.vue';
import myEntry from './components/secretEntry/Submit.vue'
// import { resolve } from 'path';

// const Posts = function(resolve){
//   require.ensure(['./components/post/Posts.vue'],
//   function(){
//     resolve(require('./components/post/Posts.vue'));
//   }, 'post')
// };

// const PostDetail = function(resolve){
//   require.ensure(['./components/post/PostDetail.vue'],
//   function(){
//     resolve(require('./components/post/PostDetail.vue'));
//   }, 'post')
// }

export const routes = [
  { path: '/', component: Home},
  { 
    path: '/posts', 
    components: {  // components not component
      default: PostHolder
    },
    children: [
      {
        path:'',
        component: Posts,
      },
      {
        path: ':id',
        component: PostDetail,
        beforeEnter: (to, from, next) => {
          next();
        }
      }
    ]
  },
  { path: '/works', component: Works },
  { path: '/about', component: About },
  { path: '/contact', component: Contact},
  { path: '/mysecretentry', component: myEntry},
];


