<template>
    <div class="card my-3">
      <div class="card-body">
        <h4 class="card-title text-center">{{currentPost.title}}</h4>
        <p v-html="compiledMarkdown.length > 140 ? compiledMarkdown.slice(0,140) + '...' : compiledMarkdown"></p>
        <div class="text-center">
          <router-link tag="a" class="btn btn-primary" :to="post.id" append>Read More...</router-link>
        </div>
      </div>
    </div>
</template>

<script>
import marked from 'marked';
export default {
  props:['post'],
  computed:{
    currentPost(){
      return this.post;
    },
    compiledMarkdown: function(){
      return marked(this.currentPost.content, {sanitize: true});
    }
  },

}
</script>



