import posts from '../../sampleData/posts';

const state = {
  posts: [],
  cur_post: {
    author: '',
    categories: [],
    content: '',
    id: '',
    title: '',
  },
};

const mutations = {
  'SET_POSTS'(state, posts){
    state.posts = posts;
  },
  'SET_CURRENTPOST'(state, post){
    state.cur_post = post;
  },
}

const actions = {
  initPosts: ({commit}) => {
    commit('SET_POSTS', posts);
  },
  setCurrentPost: ({commit}, payload) => {
    // console.log('from posts.js actions: ')
    // console.log(payload)
    commit('SET_CURRENTPOST', payload)
  }
}

const getters = {
  posts: function(state){
    return state.posts;
  },
  currentPost: function(state){
    return state.cur_post;
  }
}


export default {
  state,
  getters,
  mutations,
  actions,
}


