import Vue from 'vue';

export const loadPosts = ({commit}) => {
  // console.log('global actions')
  Vue.http.get('posts.json')
  .then(response => response.json())
  .then(data => {
    if(data){
      var postsArr = [];
      for(let key in data){
        data[key].id = key;
        postsArr.push(data[key]);
      }
      commit('SET_POSTS', postsArr);
    }
  });
};


