<template>
  <div>
    <app-post :post="post" v-for="(post, key) in posts" :key="key"></app-post>
  </div>
</template>


<script>
import Post from './Post.vue';
export default {
  components:{
    appPost: Post,
  },
  computed:{
    posts(){
      return this.$store.getters.posts;
    }
  },
}
</script>

<style>

</style>



