<template>
  <div>
    <div class="row my-5">
      <div class="col-md-6">
        <form class="">
          <div class="form-group">
            <label class="lead" for="author">Author: </label>
            <input type="text" class="form-control" id="author" placeholder="Enter Author" v-model="newPost.author">
          </div>
          <div class="form-group">
            <label class="lead" for="title">Title: </label>
            <input type="text" class="form-control" id="title" placeholder="Enter Title" v-model="newPost.title">
          </div>
          <div class="form-group">
            <label class="lead" for="category">Category: </label>
            <input type="text" class="form-control" id="category" placeholder="Enter Category (use any seperator)" v-model="newPost.cateSingle">
          </div>
          <div class="form-group">
            <label class="lead" for="content">Content: </label>
            <textarea id="content" placeholder="Enter Content" v-model="newPost.content"></textarea>
          </div>
          <button class="btn btn-primary btn-block btn-lg" @click.prevent="createPost">Submit</button>
        </form>
      </div>
      <div class="col-md-6 my-4" v-if="newPost.author">
        <div class="card">
          <div class="card-header text-center">
            {{newPost.title}} -- {{newPost.author}}
          </div>
          <div class="card-body">
            <div class="preview" v-html="compiledMD"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import marked from 'marked';
export default {
  data(){
    return {
      newPost: {
        author: '',
        content: '',
        title: '',
        cateSingle: '',
        category: [],
      }
    }
  },
  computed:{
    compiledMD(){
      return marked(this.newPost.content, {sanitize: true});
    }
  },
  methods:{
    createPost(){
      this.newPost.category.push(this.cateSingle);
      this.$http.post('posts.json', this.newPost);
    }
  },
  beforeDestroy(){
    this.$store.dispatch('loadPosts');
  }
}
</script>

<style scoped>
textarea{
  resize: none;
  display: block;
  width: 100%;
  height: 500px;
  overflow-y: auto;
}
</style>



