import Vue from 'vue';
import Vuex from 'vuex';
import posts from './modules/posts';
import works from './modules/works';
import * as actions from './actions'

Vue.use(Vuex);

export default new Vuex.Store({
  actions,
  modules:{
    posts,
    works,
  },
})


