import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import {routes} from './routes';
import 'bootstrap';
import store from './store/store';
// import Marked from './common/directives/marked';

Vue.use(VueRouter);
Vue.use(VueResource);
// Vue.use(Marked);
// https://myblog-cbce5.firebaseio.com/
Vue.http.options.root = 'https://myblog-cbce5.firebaseio.com/';


const router = new VueRouter({
  mode: 'history',
  routes: routes,
})

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



